import React from 'react';
import { dpi_height, dpi_width } from '../dimensions';
import './Frame.scss';

export function Frame (props:any) {
    return <div className="frame" style={{ width: dpi_width, height: dpi_height }} >
        <div className="frame_title">#{props.index}</div>
        <img alt={'frame' + props.index} src={props.src} width={dpi_width} height={dpi_height} />
    </div>
}