import React, { useMemo, useState, useEffect } from 'react';
import { frame } from '../core';
import parser from '../core/parser/parser';
import { loadSprite } from '../core/sprites/sprites';
import { height, width } from './dimensions';
import './Editor.scss';
import { Preview } from './Preview/Preview';
import { Subject } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import { ErrorMessage } from './ErrorMessage/ErrorMessage';


export function Editor(props: any) {
    const [scenario, setScenario] = useState(props.defaultScenario);
    const [error, setError] = useState<any>(undefined);

    // this effect and state are for debouncing text edit {{{
    // TODO: refactor
    const [onChange, setOnChange] = useState({ change: (() => void 0) as any });
    useEffect(() => {
        const subject = new Subject<string>();

        const subscription = subject
            .pipe(
                debounceTime(300)
            )
            .subscribe((value:string) => {
                setScenario(value);
            })

        setOnChange({
            change: (value: any) => subject.next(value)
        });

        return () => {
            subscription.unsubscribe();
        }
    }, [ setOnChange ]);
    // }}}

    // react to scenario change to render the frames {{{
    const frames: any[] = useMemo(() => {
        let frames = [];
        try {
            const data = parser.parse(scenario);
            frames = frame(data);
            setError(undefined);
        } catch (err) {
            setError(err);
        }

        const frameImages = frames.map((frame: any) => {
            const canvas = document.createElement('canvas');
            canvas.width = width;
            canvas.height = height;
            const ctx = canvas.getContext('2d');
            if (!ctx) {
                throw new Error('no 2d context found');
            }

            ctx.fillStyle = '#ffffff';
            ctx.fillRect(0, 0, width, height);

            const entries = Object.values(frame.objects);
            const image = Promise.all(
                entries.map((entry: any) => {
                    return loadSprite(entry.sprite, entry.state).then(img => {
                        return { img, entry }
                    });
                })
            ).then(entries => {
                entries.forEach(({ img, entry }: any) => {
                    ctx.drawImage(img, entry.state.x, entry.state.y);

                    if (entry.says) {
                        ctx.fillStyle = '#000000';
                        ctx.font = '32px serif';
                        ctx.fillText(entry.says, entry.state.x, entry.state.y);
                    }

                    ctx.drawImage(img, entry.state.x, entry.state.y);
                });

                // add watermark
                ctx.fillStyle = '#00000066';
                ctx.font = '24px monospace';
                ctx.fillText('framd.cc', 500, 460);

                return new Promise((res, rej) => {
                    // TODO: free blob url on re-render
                    canvas.toBlob((blob) => res(URL.createObjectURL(blob)));
                })
            });

            return { ...frame, image };
        });

        return frameImages;
    }, [scenario]);
    // }}}

    return <div className="editor">
            <div className="editor_scenario">
                <div className="editor_textareawrapper">
                    <textarea defaultValue={scenario} onChange={e => onChange.change(e.target.value)} />
                </div>
            </div>
            <div className="editor_preview">
                { error
                ? <div className="editor_error"><ErrorMessage error={error} /></div>
                : null
                }
                <Preview frames={ frames } />
            </div>
        </div>
}