import * as React from 'react';
import { merge, from, timer, of } from 'rxjs';
import { repeat, ignoreElements, concatMap } from 'rxjs/operators';
import { dpi_width, dpi_height } from '../dimensions';

export const AnimationPreview = (props: any) => {
    const [image, setImage] = React.useState(undefined);

    React.useEffect(() => {
        const frames = props.frames;
        if (!frames.length) {
            setImage(undefined);
            return;
        }

        const sub = from(frames)
            .pipe(
                concatMap((frame: any) => {
                    return merge(
                        of(frame),
                        timer(frame.duration).pipe(ignoreElements())
                    )
                }),
                repeat()
            )
            .subscribe((frame: any) => {
                setImage(frame.image);
            });

        // return destroy fn to unsubscribe;
        return () => sub.unsubscribe();

    }, [props.frames]);

    if (!image) {
        return null;
    }

    return <div>
        <img alt="frame" width={dpi_width} height={dpi_height} src={image} />
    </div>
}