import * as React from 'react';
import { Frame } from './Frame';
import './FramesPreview.scss';

export function FramesPreview(props: { frames: any[] }) {
    const { frames } = props;
    return <div className="frames-preview">{
        frames.map((frame, index) => <Frame key={frame.image} src={frame.image} index={index} />)
    }</div>
}