import merge from 'lodash/merge'

export function frame(data) {
    let objects;

    const frames = data.map(entry => {
        if (entry.type === 'scene') {
            objects = entry.values.reduce((acc, curr) => {
                acc[curr.objectName] =  {
                    sprite: curr.sprite,
                    state: merge({ x: 0, y: 0, scale: 1, flipped: false, turned: false }, curr.state)
                };
                return acc;
            }, Object.create(null));

            return null;
        }

        if (entry.type === 'frame') {

            const frame = {
                duration: entry.duration || 1000,
                objects: merge({}, objects)
            };

            entry.value.forEach(action => {
                if (action.type !== 'action') {
                    return null;
                }

                const key = action.key;
                const value = action.value;

                if (!(key in objects)) {
                    throw new Error(`Unknown key ${key}`);
                }

                const object = objects[key];

                if (value.type === 'sprite-action') {
                    const sprite = value.sprite;
                    frame.objects[key].sprite = sprite;
                    objects[key].sprite = sprite;
                }

                if (value.type === 'move-action') {
                    const d = value.value;
                    const shift =
                        { right: { x: d, y: 0 }
                        , left:  { x: -d, y: 0 }
                        , up:    { x: 0, y: -d }
                        , down:  { x: 0, y: d }
                        }[value.direction];

                    const update = merge({}, object.state, { x: object.state.x + shift.x, y: object.state.y + shift.y });
                    frame.objects[key].state = update;
                    objects[key].state = update;
                }

                if (value.type === 'turn-action') {
                    const turned = !objects[key].state.turned;
                    const update = merge({}, objects[key].state, { turned });
                    frame.objects[key].state = update;
                    objects[key].state = update;
                }

                if (value.type === 'flip-action') {
                    const flipped = !objects[key].state.flipped;
                    const update = merge({}, objects[key].state, { flipped });
                    frame.objects[key].state = update;
                    objects[key].state = update;
                }

                if (value.type === 'scale-action') {
                    const scale = object.state.scale * value.times;
                    const update = merge({}, objects[key].state, { scale });
                    frame.objects[key].state = update;
                    objects[key].state = update;
                }

                if (value.type === 'state-action') {
                    const update = merge({}, objects[key].state, value.value);;
                    frame.objects[key].state = update;
                    objects[key].state = update;
                }

                if (value.type === 'say-action') {
                    frame.objects[key].says = value.value;
                }
            });

            return frame;
        }

        throw new Error('Unknown entry');
    })
    .filter(x=>x);

    return frames;
}