import * as React from 'react';
import { width, height } from '../dimensions';
const GIF  = (window as any)['GIF'];

    // const gifOutputRef = createRef<HTMLImageElement>();
    // const outputLinkRef = createRef<HTMLAnchorElement>();



export const ExportGif = (props: { frames: any[] }) => {
    const { frames } = props;

    const onClick = React.useCallback(() => {
        const gif = new (GIF as any)({
            workers: 2,
            quality: 10,
            workerScript: process.env.PUBLIC_URL + '/gif.worker.js',
            width,
            height
        });

        frames.forEach((frame, i) => {
            const img = new Image();
            img.src = frame.image;
            gif.addFrame(img, { delay: frame.duration });
        });

        gif.on('finished', function (blob: any) {
            // TODO: destroy object url
            const src = URL.createObjectURL(blob);
            const link = document.createElement('a');
            link.href = src;
            link.download = 'animato.gif';
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);

            gif.freeWorkers.forEach((w: any) => w.terminate());
        });

        try {
            gif.render();
        } catch (e) {
            console.error(e);
        }

        return () => {
            gif.destroy();
        }
    }, [frames]);

    return <button onClick={onClick}>Download .gif</button>;
}