import * as React from 'react';
import { templates } from './examples';
import { dpi_height, dpi_width } from '../Editor/dimensions';
import './Landing.scss';

export const LandingPage = (props: any) => {
    const { onTemplateSelect } = props;
    return <div className="landing">
        <div className="landing-title">
            <h1>Choose a template to Start:</h1>
        </div>
        
        <div className="landing-templates">
        {
            templates.map(d => {
                return (
                    <div
                        className="landing-template"
                        key={d.title}
                        onClick={ () => onTemplateSelect(d.scenario) }
                    >
                        <div className="landing-template-title">{ d.title }</div>
                        <img
                            className="landing-template-preview"
                            alt={d.title + ' preview'}
                            src={d.preview}
                            width={dpi_width / 2}
                            height={dpi_height / 2}
                            />
                    </div>
                )
            })
        }</div>
    </div>
}