const EMOJI_PROTOCOL = 'emoji:';

export function loadSprite(sprite:string, state: any) {
    const protocol = (new URL(sprite)).protocol;
    const url = sprite.substr(protocol.length);
    if (protocol === EMOJI_PROTOCOL) {
        return loadEmojiSprite(url, state);
    }

    throw new Error('Non emoji symbols are not supported yet');
}

function loadEmojiSprite(emoji: string, state: any) {
    return new Promise((res, rej) => {
        const canvas = document.createElement('canvas');
        const fontsize = 86 * (state?.scale ?? 1);
        const size = fontsize * 1.2;
        const ctx = canvas.getContext('2d');
        if (!ctx) {
            rej('No context 2d found');
            return;
        }

        canvas.width = size;
        canvas.height = size;
        ctx.font = fontsize + 'px serif';

        if (state?.flipped || state?.turned){ 
            if (state?.turned) {
                ctx.scale(-1, 1);
                ctx.translate(-size, 0)
            }

            if (state?.flipped) {
                ctx.scale(1, -1);
                ctx.translate(0, -size)
            }

            if (state?.flipped && state?.turned) {
                ctx.scale(-1, -1);
                ctx.translate(-size, -size)
            }
        }

        ctx.fillText(emoji, 0, fontsize);
        ctx.restore();
        res(canvas);
    })
}
