import React, { useEffect, useState } from 'react';
import { AnimationPreview } from './AnimationPreview';
import { ExportGif } from './ExportGif';
import { FramesPreview } from './FramesPreview';
import './Preview.scss';


export const Preview = (props: { frames: any[] }) => {
    const [frames, setFrames] = useState([] as any[]);

    useEffect(() => {
        // waits for frame.image promise to resolve
        // then replaces that promise with resulting image
        // TODO: refactor this
        Promise.all(
            props.frames.map(frame => frame.image.then((image: any) => ({ ...frame, image })))
        )
        .then(setFrames)
    }, [props.frames]);

    if (!frames.length) {
        return null;
    }

    return <div className="preview">
        <AnimationPreview frames={frames} />

        <ExportGif frames={frames}/>

        <FramesPreview frames={frames} />
    </div>
}